"use client";

import Navbar from "../../../components/Navbar";
import { useState, useEffect } from "react";

export default function OrdersPage() {
  const [cartItems, setCartItems] = useState<any[]>([]);

  // Load cart from localStorage
  useEffect(() => {
    const savedCart = localStorage.getItem("cart");
    if (savedCart) {
      setCartItems(JSON.parse(savedCart));
    }
  }, []);

  const handlePurchase = () => {
    if (cartItems.length === 0) {
      alert("ðŸ›’ Your cart is empty!");
      return;
    }
    alert("âœ… Order placed successfully!");
    setCartItems([]);
    localStorage.removeItem("cart");
  };

  // Calculate total amount
  const totalAmount = cartItems.reduce((total, item) => total + item.price, 0);

  return (
    <div className="bg-gray-50 min-h-screen text-gray-900">
      <Navbar />
      <div className="max-w-4xl mx-auto p-6">
        <h2 className="text-3xl font-bold mb-6 text-center text-gray-900">
          Your Order
        </h2>
        
        {cartItems.length === 0 ? (
          <p className="text-center text-gray-500">Your cart is empty.</p>
        ) : (
          <div className="bg-white rounded-xl shadow-lg p-6 space-y-4">
            {cartItems.map((item, index) => (
              <div
                key={index}
                className="flex items-center gap-4 border-b pb-4 last:border-b-0"
              >
                <img
                  src={item.image || "https://upload.wikimedia.org/wikipedia/commons/6/65/Croissant-Petr_Kratochvil.jpg"}
                  alt={item.name}
                  className="w-20 h-20 object-cover rounded-lg shadow"
                />
                <div className="flex-1">
                  <h3 className="text-lg font-semibold text-gray-900">{item.name}</h3>
                  <p className="text-gray-600">{item.description}</p>
                </div>
                <p className="font-bold text-lg text-gray-900">â‚¹{item.price}</p>
              </div>
            ))}

            {/* Total */}
            <div className="flex justify-between items-center mt-6 pt-4 border-t">
              <span className="text-xl font-semibold text-gray-900">Total</span>
              <span className="text-xl font-bold text-gray-900">â‚¹{totalAmount}</span>
            </div>

            {/* Place Order Button */}
            <button
              onClick={handlePurchase}
              className="w-full mt-4 bg-yellow-500 hover:bg-yellow-600 text-white font-bold py-3 rounded-lg shadow-md transition"
            >
              Place Order
            </button>
          </div>
        )}
      </div>
    </div>
  );
}
